    public * createStraightGen(weight: number, count: number){
        let notValid = (DdzDriver.instance.Context.mode == DdzMode.Normal && count < 5) || (DdzDriver.instance.Context.mode == DdzMode.Quick && count < 3);
        if(notValid)
            return -1;
        let orderNumArr = this.typeCheckHelper.getOrderBiggerNumArr(this.numMap, 1);
        let startWeight = weight - count + 1;                   // 顺子起点权重 3-7  7-5+1=3 至少从4开始的顺子
        for(let i=0; i< orderNumArr.length; i++){
            let value = orderNumArr[i];
            let [startW] = DdzPokerCheck.getPokerWeight(value);
            if (startW > startWeight){                          // 1-先找起点
                let wantEndWeight = startW + count - 1;
                let endValue = orderNumArr[i + count - 1];
                if (wantEndWeight <= 14 && endValue){           // orderNumArr是按照权重升序 故可以这么判断连续
                    let [endW] = DdzPokerCheck.getPokerWeight(endValue);
                    if (endW == wantEndWeight){                 // 2--然后找终点存在，并确保起点到终点连续
                        let tmp = {};
                        for(let k = 0; k < count; k++){         // 3---最后起点到终点间的牌
                            for(let j =0; j< this.curArr.length; j++){
                                let rawValue = this.curArr[j];
                                let [pokerValue] = DdzPokerCheck.getPokerValue(rawValue);
                                if (pokerValue == orderNumArr[i + k]){     
                                    tmp[j] = rawValue;
                                    break;
                                }
                            }
                        }
                        yield tmp;
                    }
                }
            }
        }

        if (this.bombCatchArr && this.bombCatchArr.length > 0){
            for(let i=0; i< this.bombCatchArr.length; i++){
                yield this.bombCatchArr[i];
            }
        }

        if (this.rocketCatch && !Global.Toolkit.isEmptyObject(this.rocketCatch)){
            yield this.rocketCatch;
        }
        
        return -1;
    }

